


































































































































import {Component, Mixins} from 'vue-property-decorator';
import {InstanceI} from '@/interfaces/instances';
import refreshAppInfo from '@/mixins/refreshAppInfo.vue';
import Modal from 'bootstrap/js/dist/modal';

// eslint-disable-next-line
// @ts-ignore
import ClickOutside from 'vue-click-outside'
import authFetch from '@/utilities/authFetch';

@Component({
  directives: {
    ClickOutside,
  }
})
export default class Instances extends Mixins(refreshAppInfo) {
  selected: InstanceI[] = [];
  loaded = false;

  modalSelectedVolume = '';


  // adds an instance to the selected array if the user is holding shift, otherwise it
  // replaces the array with the instance
  selectionHandler(event: MouseEvent, instance: InstanceI): void {
    if (event.ctrlKey) {
      this.selected.push(instance);
    } else {
      this.selected = [instance];
    }
  }

  async mountVolumes(newVolumeID: string, selected: InstanceI[]): Promise<void> {
    for (const instance of selected) {
      await authFetch(`${this.$store.state.adminAPIURL}/admin/updateInstance`, {
        method: 'POST',
        headers: {
          'Content-Type': 'application/json',
        },
        body: JSON.stringify({
          id: instance._id,
          instance: {
            volumeID: newVolumeID,
          },
        }),
      });
    }

    await this.getInstances();
  }

  async renameInstance(instance: InstanceI): Promise<void> {
    const newName = await prompt('New instance name', instance.name);

    if (newName) {
      await authFetch(`${this.$store.state.adminAPIURL}/admin/updateInstance`, {
        method: 'POST',
        headers: {
          'Content-Type': 'application/json',
        },
        body: JSON.stringify({
          id: instance._id,
          instance: {
            name: newName,
          },
        }),
      });
    }

    await this.getInstances();
  }

  async deleteInstances(instances: InstanceI[]): Promise<void> {
    const confirmed = await confirm(`Are you sure you want to delete ALL SELECTED INSTANCES?`);

    if (!confirmed) return;

    for (const instance of instances) {
      await authFetch(`${this.$store.state.adminAPIURL}/admin/deleteInstance`, {
        method: 'POST',
        headers: {
          'Content-Type': 'application/json',
        },
        body: JSON.stringify({
          id: instance._id,
        }),
      });
    }

    await this.getInstances();
  }

  async duplicateInstance(instance: InstanceI): Promise<void> {
    const newName = await prompt('Duplicated instance name', instance.name);

    if (newName) {
      const createRes = await authFetch(`${this.$store.state.adminAPIURL}/admin/createInstance`, {
        method: 'POST',
        headers: {
          'Content-Type': 'application/json',
        },
        body: JSON.stringify({
          name: newName,
        }),
      });

      const resJson = await createRes.json();

      const newInstance: InstanceI = {
        volumeID: instance.volumeID,
        name: newName,
        _id: resJson.instanceID,
      }

      await this.mountVolumes(instance.volumeID || '', [newInstance]);
    }
  }


  async createInstance(): Promise<void> {
    const newName = await prompt('New instance name');
    
    if (newName) {
      await authFetch(`${this.$store.state.adminAPIURL}/admin/createInstance`, {
        method: 'POST',
        headers: {
          'Content-Type': 'application/json',
        },
        body: JSON.stringify({
          name: newName,
        }),
      });
    }

    this.getInstances();
  }

  toggleModal(on: boolean): void {
    if (!this.$refs.modal) return;
    const modal = new Modal(this.$refs.modal as HTMLElement, {});

    (on) ? modal.show() : modal.hide();
  }

  async getExecutionPort(): Promise<void> {
    const portRaw = await authFetch(`${this.$store.state.adminAPIURL}/admin/getExecutionPort`);
    const port = await portRaw.json();

    this.$store.commit('setExecutionAPIURL', location.protocol + '//' + location.hostname + ':' + port.executionPort);
  }

  async mounted(): Promise<void> {
    await this.getVolumes();
    await this.getInstances();
    await this.getExecutionPort();

    this.loaded = true;
  }
}
